$desktopPadding: 6px;
$mobilePadding: 3px;

$cellBorderRadius: 6px;

.root {
  border-radius: 9px;
  background: linear-gradient(
    135.94deg,
    #f3e7ff 1.59%,
    rgba(243, 231, 255, 0.4) 50.28%,
    #f3e7ff 100%
  );
  transition: padding 0.3s, border-radius 0.1s, transform 0.1s;
  &:hover {
    cursor: pointer;
    transform: scale(1.03);
  }
}

.selected {
  padding: $desktopPadding;
  border-radius: $cellBorderRadius;
  @media (max-width: 530px) {
    padding: $mobilePadding;
  }
}

.blocked {
  filter: brightness(0.5);
}

.notClickable {
  pointer-events: none;
}

.cell {
  position: relative;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Gilroy";
  font-weight: 900;
  font-size: 24px;
  color: white;
  user-select: none;
  background: #634087;
  border-radius: $cellBorderRadius;
  & > span {
    z-index: 2;
    position: relative;
  }
  @media (max-width: 530px) {
    font-size: 14px;
  }
}

.opened {
  & > div {
    background: #240048;
  }
}

.reward {
  & > div {
    color: rgba(36, 0, 72, 1);
  }
  @media (max-width: 530px) {
    padding: $mobilePadding;
  }
}

.selectedReward {
  background: linear-gradient(
    135.94deg,
    #f3e7ff 1.59%,
    #240048 50.28%,
    #f3e7ff 100%
  );
  padding: $desktopPadding;
}

@keyframes showLoss {
  from {
    transform: scale(0.5);
    opacity: 0;
  }
  75% {
    transform: scale(1.1);
    opacity: 1;
  }
  to {
    transform: scale(1);
  }
}

.empty {
  color: rgba(198, 0, 130, 1);
  animation-name: showLoss;
  animation-duration: 0.5s;
}

@keyframes showReward {
  from {
    transform: scale(0);
  }
  75% {
    transform: scale(1.2);
  }
  to {
    transform: scale(1.1);
  }
}

.diamondIcon {
  position: absolute;
  width: 100%;
  height: 100%;
  transform: scale(1.1);
  animation-name: showReward;
  animation-duration: 0.5s;
}
