$desktopRowCount: 5;
$desktopColumnCount: 8;
$desktopFieldWidth: 35vw;

$mobileRowCount: 8;
$mobileColumnCount: 5;
$mobileFieldWidth: 83vw;

.root {
  width: $desktopFieldWidth;
  height: calc(
    (#{$desktopFieldWidth} / #{$desktopColumnCount}) * #{$desktopRowCount}
  );
  max-width: 550px;
  max-height: 340px;
  @media (max-width: 1250px) {
    width: $mobileFieldWidth;
    height: calc(
      (#{$mobileFieldWidth} / #{$desktopColumnCount}) * #{$desktopRowCount}
    );
  }
  @media (max-width: 530px) {
    max-width: 280px;
    max-height: 451px;
    height: calc(
      (#{$mobileFieldWidth} / #{$mobileColumnCount}) * #{$mobileRowCount}
    );
  }
}

.field {
  display: grid;
  height: 100%;
  grid-template-rows: repeat(5, 1fr);
  grid-template-columns: repeat(8, 1fr);
  gap: 10px;
  @media (max-width: 960px) {
    gap: 5px;
  }
  @media (max-width: 530px) {
    grid-template-rows: repeat(8, 1fr);
    grid-template-columns: repeat(5, 1fr);
  }
}
