@import "sass/index";

.root {
  position: relative;
  padding-bottom: 10px;
}

.payouts {
  width: 100%;
  display: flex;
  justify-content: center;
  & > div:not(:last-child) {
    margin-right: 10px;
  }
  & > div {
    max-width: 30%;
  }
  @media (max-width: 950px) {
    & > div:not(:last-child) {
      margin-right: 5px;
    }
  }
}

.profit {
  position: absolute;
  width: 100%;
  z-index: 5;
  bottom: 120%;
}

.selectRowsText {
  display: inline-block;
  width: 100%;
  user-select: none;
  font-family: "Gilroy";
  font-weight: bold;
  font-size: 16px;
  color: use(--template-input-topLabel-color);
  text-align: center;
}
