@import "sass/index";
@import "config/themes/_diamonds";

$fontSize: 2vw;

.root {
  position: relative;
  width: 100%;
  text-align: center;
  font-family: "Gilroy";
  font-weight: bold;
  font-size: Min(#{$fontSize}, 16px);
  color: use(--template-input-topLabel-color);
  user-select: none;
  &:hover {
    & .pointer {
      display: block;
    }
  }
}

.icon {
  width: Min(#{$fontSize}, 18px);
  height: Min(#{$fontSize}, 18px);
  filter: grayscale(1);
  transform: scale(1.2);
  margin-left: 2px;
}

.win {
  color: rgb(122, 210, 91) !important;
  & > svg {
    filter: none;
  }
}

.ratio {
  padding-top: 5.5px;
  padding-bottom: 5.5px;
  border: 1px solid use(--template-footer-logos-pasino-background, 0.2);
  width: 100%;
  text-align: center;
  border-radius: 6px;
  margin-bottom: 5px;
}

.hit {
  display: flex;
  justify-content: center;
  align-items: center;
}

.pointer {
  display: none;
  position: absolute;
  z-index: 10;
  left: calc(50% - 6px);
  bottom: 111%;
  transform: rotate(45deg);
  width: 14px;
  height: 14px;
  background-color: $blackCurrant;
  border-right: 1px solid $bossaNova;
  border-bottom: 1px solid $bossaNova;
}
