@import "sass/index";
@import "config/themes/_diamonds";

.root {
  display: inline-block;
  width: 100%;
  & > h5 {
    margin: 0;
    margin-bottom: 5px;
    font-family: "Gilroy";
    font-weight: bold;
    font-size: 14px;
    color: white;
  }
}

.field {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: rgba($white, 0.05);
  border-radius: 6px;
  font-size: 16px;
  font-family: "Gilroy";
  color: use(--template-input-topLabel-color);
  border: 1px solid $bossaNova;
  padding: 10px;
}

.coinIcon {
  position: static;
  transform: scale(1.3);
  user-drag: none;
}
