@import "sass/index";

.root {
  display: flex;
  background-color: $blackCurrant;
  padding: 13px;
  border-radius: 6px;
  & > div:not(:last-child) {
    margin-right: 10px;
  }
  width: 100%;
  border: 1px solid $bossaNova;
  @media (max-width: 530px) {
    flex-direction: column;
    padding-left: 8%;
    padding-right: 8%;
    & > div:not(:last-child) {
      margin-right: 0;
      margin-bottom: 10px;
    }
  }
}
